<template>
  
 

      <!-- heurs  -->
      <div v-if="time_display" class="horloge   pt-0 d-flex justify-content-center ">
      
      
      <div class=" hours  pt-0" >
        {{ time.substring(0, 5) }}
      </div>

      <div class="secondes m-0 pt-2   ">
        {{ time.substring(6, 8) }}
      </div>
      </div>

  
  



    <div v-if="date_display"  class="bg-1 date  rounded pb-1 ">
      {{ date }}
    </div>

  

    <!-- #################################### -->
  





</template>

<script>


// import cosigneWidget from '@/components/dashboard/Cons.vue'
export default {
  name: 'hotloge',
  props: {
    date_display:{default:false},
    time_display:{default:false},
  },
  components:{},

  data() {
    return {
      time: '',
      date:'',
    };
  },
  mounted() {
    this.startClock();
  },
  methods: {
    startClock() {
      setInterval(this.updateClock, 1000);
    },
    updateClock() {
      const now = new Date();
      const hours = String(now.getHours()).padStart(2, '0');
      const minutes = String(now.getMinutes()).padStart(2, '0');
      const seconds = String(now.getSeconds()).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const year = now.getFullYear();

      this.time = `${hours}:${minutes}:${seconds}`;
      this.date = `${day}-${month}-${year}`;
    }
  },


}
</script>

