export function collaps(idc,classc,id){
    
    var container = document.getElementById(idc)
    var content = document.getElementById(id)
    var arrow = document.getElementById(`${idc}arrow`)

    // console.log(container)
    // console.log(content)
    // console.log(arrow)

    container.classList.toggle("open")
    container.classList.toggle("pb-0")
    content.classList.toggle("d-none")
    arrow.classList.toggle("rotate-arrow")
    
    const all_containers = document.getElementsByClassName(classc)
    // console.log(all_containers)
    for(let i = 0 ; i < all_containers.length; i++ ){
      const id = all_containers[i].id
      // console.log(all_containers[i])
      
      if (id != idc){
        container = document.getElementById(id)
        arrow = document.getElementById(`${id}arrow`)
        // Sélectionnez le 3eme élément <div> enfant
        content = container.querySelectorAll('div')[2];
        
        // console.log(container)
        if(container.classList.contains('open')){
          container.classList.remove("open")
        }
        if(!container.classList.contains("d-none")){
          content.classList.add("d-none")
        }
      
        arrow.classList.remove("rotate-arrow")
      }

    }

}

export function verify_pass(string,sixchar,capital,lower,numb,specialchar){
      
  var pass_valid = true

  // Les regex
  var numbRegex = /\d/;
  var capitalRegex = /[A-Z]/;
  var lowerRegex = /[a-z]/;
  var specialRegex = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/;

  // Vérification
  var contientChiffre = numbRegex.test(string);
  var contientMajuscule = capitalRegex.test(string);
  var contientMinuscule = lowerRegex.test(string);
  var contientSpecial = specialRegex.test(string);


  // Config elimination 
  // if (contientChiffre){numb.classList.add('d-none')}else{numb.classList.remove('d-none')}
  // if (contientMajuscule){capital.classList.add('d-none')}else{capital.classList.remove('d-none')}
  // if (contientMinuscule){lower.classList.add('d-none')}else{lower.classList.remove('d-none')}
  // if (contientSpecial){specialchar.classList.add('d-none')}else{specialchar.classList.remove('d-none')}
  // if (string.length >= 6){sixchar.classList.add('d-none')}else{sixchar.classList.remove('d-none')}
  
  // Config coloration 

  ////////////////////////////
  if (contientChiffre){
    
  numb.classList.add('color-green'); 
  numb.classList.remove('color-red'); 
  numb.querySelector('span').classList.remove('d-none')

  }else{
    numb.classList.remove('color-green');
    numb.classList.add('color-red'); pass_valid = false
    numb.querySelector('span').classList.add('d-none')

  }
  ////////////////////////////
  if (contientMajuscule){ 
    capital.classList.add('color-green'); 
    capital.classList.remove('color-red');
    capital.querySelector('span').classList.remove('d-none')
  }else{
    capital.classList.remove('color-green');
    capital.classList.add('color-red'); pass_valid = false
    capital.querySelector('span').classList.add('d-none')

  }
  ////////////////////////////
  if (contientMinuscule){
    lower.classList.add('color-green'); 
    lower.classList.remove('color-red'); 
    lower.querySelector('span').classList.remove('d-none')

  }else{
    lower.classList.remove('color-green');
    lower.classList.add('color-red'); pass_valid = false
    lower.querySelector('span').classList.add('d-none')

  }
  ////////////////////////////
  if (contientSpecial){
    specialchar.classList.add('color-green'); 
    specialchar.classList.remove('color-red');  
    specialchar.querySelector('span').classList.remove('d-none')

  }else{
    specialchar.classList.remove('color-green');
    specialchar.classList.add('color-red'); pass_valid = false
    specialchar.querySelector('span').classList.add('d-none')

  }
  ////////////////////////////
  if (string.length >= 6){
    sixchar.classList.add('color-green'); 
    sixchar.classList.remove('color-red');  
    sixchar.querySelector('span').classList.remove('d-none')

  }else{
    sixchar.classList.remove('color-green');
    sixchar.classList.add('color-red'); pass_valid = false
    sixchar.querySelector('span').classList.add('d-none')

  }
  ////////////////////////////



  if (pass_valid == true){
    console.log("valiiiiiiiid")
    return true
  }else{ return false}

}

