import { createRouter, createWebHashHistory } from 'vue-router'


import HomeV from '@/views/platform/Home.vue'
import DashV from '@/views/platform/Dash.vue'
import AdminDashV from '@/views/platform/AdminDash.vue'
import CaisseV from '@/views/platform/Caisse.vue'
import CaissesManageV from '@/views/platform/CaissesManage.vue'
import UserProfilV from '@/views/platform/UserProfil.vue'

import SignInMainV from '@/views/SignInMain.vue'
import SignInSecV from '@/views/SignInSec.vue'

import FirstSecUserV from '@/views/utile/FirstSecUser.vue'
import FirstPassV from '@/views/utile/FirstPass.vue'
import SignUpV from '@/views/utile/SignUp.vue'




var routes = []


var pages = [
  ['',"home",HomeV],
  ['dash',"dash",DashV],
  ['caisse',"caisse",CaisseV],
  ['caissesmanage'," caissesmanage", CaissesManageV],
  ['addash',"addash",AdminDashV],
  ['userprofil',"userprofil",UserProfilV],

  ['signinsec',"signinsec",SignInSecV],
  ['signinmain',"signinmain",SignInMainV],
  
  
  ['signup',"signup",SignUpV],
  ['firstpass',"firstpass",FirstPassV],
  ['firstsecuser',"firstsecuser",FirstSecUserV],

  // ['preferances',"Preferances",PreferancesView],

]



pages.forEach(page => {
  const obj = {
    path: `/${page[0]}`,
    name: page[1],
    component: page[2]
  }

  routes.push(obj)
  
});





const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
