  },
  mounted() {
    this.observeFormVisibility();
  },
  updated() {
    this.observeFormVisibility();
  },
  methods: {
    observeFormVisibility() {
      // Vérifier si le formulaire est visible et n'est pas nul
      if (this.$refs.form && this.$refs.form.offsetHeight !== 0) {
        // Le formulaire est visible, exécuter votre fonction
        this.form_opened();
      } else {
        // Le formulaire n'est pas visible, réinitialiser la hauteur du conteneur
        this.resetContainerHeight();
      }
    },
    form_opened() {
      const container = this.$refs.container;
      const form = this.$refs.form;
      if (container && form) {
        var formHeight = form.offsetHeight + 45;
        container.style.height = `calc(100% - ${formHeight}px)`;
      }
    },
    resetContainerHeight() {
      const container = this.$refs.container;
      if (container) {
        container.style.height = 'auto'; // Réinitialiser la hauteur à auto
      }
    },
    //////////////////////////////////////////////////////////
    edite_user_func(user){
      this.user=user

      this.edit_service = false
      this.add_user= false
      this.add_service = false
      this.edite_user = true
      // this.user = 
    },
    cxl_edite_user(){
      this.edite_user = false 
      this.user = {
        obj_name:"employe",
        first_name:"",
        last_name:"",
        email:"",
        utilisateur:"",
        phone:"",
        poste:"",
        password1:"",
        password2:"",
      }
    },
    call_verify_pass(){
      
      // Les phrase d'information conditions mot de passe
      const sixchar = document.getElementById("sixcar")
      const capital = document.getElementById("capital")
      const lower = document.getElementById("lower")
      const numb = document.getElementById("numb")
      const specialchar = document.getElementById("specialchar")

      let pass_validation = verify_pass(this.user.password1,sixchar,capital,lower,numb,specialchar)

      console.log(pass_validation)






    },

  }

}
