<script>
import { myuser_service_liste,myconges_liste } from '@/data'


export default {
  name: 'MesConges',
  props: {
    cp_liste:{default:[]},
    
  },
  data(){return{
    demande_cp:false,
   
    cp:{
      date_du:"",
      date_au:"",
      date:"",
      motif:"",
      accepted:"",
    },

    jours_cp:0,

  }},

  mounted(){console.log("cppppp");console.log(this.cp_liste)},
  methods:{

  },
  watch: {
    
    // calcule nombre de jour cp 
    'cp.date_au'(newVal, oldVal) {
      let date_du = this.cp['date_du']
      let date_au = this.cp['date_au']
      if (date_du.length > 0 & date_au.length > 0){
         date_du = new Date(this.cp['date_du'])
         date_au = new Date(this.cp['date_au'])
         const diffTime = Math.abs(date_au - date_du);
        this.jours_cp = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      }      
    },
    'cp.date_du'(newVal, oldVal) {
      let date_du = this.cp['date_du']
      let date_au = this.cp['date_au']
      if (date_du.length > 0 & date_au.length > 0){
         date_du = new Date(this.cp['date_du'])
         date_au = new Date(this.cp['date_au'])
         const diffTime = Math.abs(date_au - date_du);
        this.jours_cp = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      }      
    },
    // FIN calcule nombre de jour cp 

}

}
</script>

