      console.log(pass_validation)






    }

  },
  watch: {
    edit_profile(newVal) {
        let inputs = document.getElementsByClassName("input");
        if (newVal === true) {
            for (let i = 0; i < inputs.length; i++) {
                inputs[i].disabled = false;
            }
        }else{
          for (let i = 0; i < inputs.length; i++) {
                inputs[i].disabled = true;
            }

        }
    },


}

