   <!-- HEAD -->
   <div class="page-head d-flex  w-100 mb-1 fw-bold" :class="{'main_dark':this.$store.state.dark == true,}">
    <h1 class="  w-100 mb-1 fw-bold" :class="{'main_dark':this.$store.state.dark == true,}">Connexion</h1>
    <DarkThemeBtn/>
  </div>

  <!-- CORP  -->
  <div  class="w-100  h-100 d-flex align-items-center ">
    
    <div class="FormContainer m-auto text-center p-4 rounded " :class="{'dark_1':this.$store.state.dark == true,}">
      
      <!-- logo  -->
      <!-- <img :src="this.api + this.$store.state.main_us.logo" alt="" style="width:150px; height:90px"> -->
      <img src="@/assets/icones/ocea.jpg" alt="" style="width:150px; height:90px">
     
      <!-- titre  -->
