      <!-- log in / password recup form  -->
      <form @submit.prevent="Login" class="text-start   w-100">
        
        <!-- password recup form  -->
        <div v-if="this.passwordRecup" class="form-group passwordRecup border rounded text-end pt-0" :class="{'border-dark':this.$store.state.dark == false,}">
        <img @click="passwordRecup = false" src="../assets/icones/cancel3.png" alt="close" class="me-2 mt-1" style="width:20px; height:20px">
          <div class="container-fluide text-center p-3">
            <strong class="text-center">Vous avez oubliez votre mot de passe ? </strong>
            <p class="ms-2 small text-center">Entrez votre utilisateur pour recevoir un mail de recuperation</p>
            <input type="utilisateur" class="form-control" id="utilisateur" v-model="utilisateur" placeholder="Entre votre utilisateur" />
            <button type="submit" class=" valide-btn rounded w-100 mt-2" >Envoyer</button>
          </div>
        </div>

        <!-- log in form  -->
        <div v-if="!this.passwordRecup" class="container fluide">
              <div v-if="conexionfailedMessage.length > 0" class="container-fluide text-center"><span class="errors">{{ conexionfailedMessage }}</span></div>

              <!-- utilisateur  -->
              <div class="form-group">
                <label for="utilisateur" class="ms-2 small fw-bold"  >Utilisateur:</label>
                <input type="utilisateur" class="form-control" id="utilisateur" v-model="utilisateur" placeholder="Entre votre nom d'utilisateur"/>
                <!-- <div v-if="EmailErrors">
                  <ul>
                    <li v-for="Error in emailErrors" class="extraSmall"> {{ Error }}</li>
                  </ul>
                </div> -->
              </div>

              <!-- password  -->
              <div class="form-group">
                <label for="password11" class="ms-2 small fw-bold">Mot de passe:</label>
                <input type="password" class="form-control" id="password11" v-model="password" placeholder="Entre votre mot de passe" />
                <!-- <div v-if="PasswordErrors">
                  <ul>
                    <li v-for="Error in passwordErrors" class="extraSmall"> {{ Error }}</li>
                  </ul>
                </div> -->
              </div>

              <!-- button log in  -->
              <div class="form-group mt-4 mb-2 text-center">
                <button type="submit" class=" valide-btn rounded w-100" >conexion</button>
              </div>

        </div>

        <!-- fergot password  -->
        <div class="mt-3">
          <div class="extraSmall" >Mot de passe oublié ?  <span  @click="this.passwordRecup = true" class="text-primary" style="cursor: pointer;">click ici</span> </div>
        </div>

        

