</template>

<script>


// import { axios_request,handel_errors} from '@/util'


import config from '@/config.js'
import DarkThemeBtn from '@/components/DarkThemeBtn.vue'


export default {
  // name: 'HomeView',
  components:{DarkThemeBtn},

  data(){return{
    api : config.apiUrl,
    utilisateur: '',
    password: '',
    logedIn:false,
    conexionfailedMessage:'',
    tentatives:0,
    passwordRecup:false,
      
   

    }
  },
  mounted(){
   
  },
  methods:{
    async Login() {
      this.conexionfailedMessage = ''
      try {
        const passwordHash = CryptoJS.SHA256(this.password).toString()
        let url = `${config.apiUrl}secsignin`
        let payload = { utilisateur: this.utilisateur , password: passwordHash}
        const response = await axios.post(url, payload)

        // AJOUTER COOKIE AU NAVIGATEUR
        Cookies.set('ustok', response.data.jwt)
        location.reload()
        // this.$store.dispatch('checkUser')      
      } catch (err) {
        console.error(err)

        if (err.response && err.response.data.detail == "Incorect username or password"){
          this.conexionfailedMessage = "utilisateur ou Mot de passe invalide, Merci de rectifier"
          this.tentatives += 1
          if (this.tentatives == 4 ){
            this.passwordRecup =true
          }
        }
      }
    }
   
  }

}
</script>

