<script>

import config from '@/config.js'
// import { axios_request} from '@/util'
// import { PDFDocument, rgb , StandardFonts, } from 'pdf-lib';





export default {
  props:{caissee:{default:{}
    
  }},
 

  data(){
    return {
    
    caisse:this.caissee,
    caisse_precedente:null,
    caisse_suivante:null,
    };
  },
  mounted(){
    // console.log("DISPLAY CAISSE COMPONENT")
    // console.log(this.caisse)
    // this. get_caisse_prec_suiv('precedente','main')
  },
  methods:{

    async get_caisse_prec_suiv(type,mode=null){

      let url = `${config.apiUrl}dispcaisse`
      var id = this.caisse['id']
      
      if (type === 'precedente' && mode != 'main'){
        if (this.caisse_precedente != null){
          this.caisse = this.caisse_precedente
          console.log("----- precedente -----")
          // console.log(this.caisse_precedente)
          this.get_caisse_prec_suiv('precedente','main')
          return
        }else{return}
      
      }else if (type === 'suivante' && mode != 'main'){
        if (this.caisse_suivante != null){
          this.caisse = this.caisse_suivante
          console.log("----- suivante -----")
          // console.log(this.caisse_suivante)
          this.get_caisse_prec_suiv('suivante','main')
          return
        }else{return}
      }

      
      let response = axios_request("GET",url,{"caisse-id":id})

      .then(response => {
        // console.log(response.data);
        const data = response.data
        console.log("--------data-------")
        console.log(data)
        this.caisse_precedente = data['caisse_precedente']
        this.caisse_suivante = data['caisse_suivante']
        // if (data['caisse_precedente'] != null){ this.caisse_precedente = data['caisse_precedente'] }
        // if (data['caisse_suivante'] != null){ this.caisse_suivante = data['caisse_suivante'] }

    
      })
      .catch(error => {
        console.error("Error:", error.response);
        let status = error.response.status
        if (status == 401 || status == 403){
          window.location.reload()
        }else if(status == 500){
          let message = ['error','Erreur serveur merci de contacter le support.']
          this.$store.state.messages = []
          this.$store.state.messages.push(message)
          console.log(this.$store.state)
        }
      });
    },

    print(){
      window.print()
    },
    
    async create_caissePDF( caisse,caisse_prec= null, caisse_suiv = null) {
      const pdfDoc = await PDFDocument.create();
      const page = pdfDoc.addPage();
      const pageWidth = page.getWidth();
      const pageHeight = page.getHeight();
      var fontSize = 10

      // Charger une police standard pour le document
      var font = await pdfDoc.embedFont(StandardFonts.Helvetica);

      var entetY = 150
      /////////// HEAD ////////////////////////////////////////////////////////////////////////////////////////////////////////
      
      // Ajoutez une image depuis un fichier local ou une URL (remplacez 'imagePath' par votre chemin d'image)
      const jpgUrl = config.apiUrl + this.$store.state.main_us["logo"];
      const jpgImageBytes = await fetch(jpgUrl).then((res) => res.arrayBuffer());
      const jpgImage = await pdfDoc.embedJpg(jpgImageBytes);
      const jpgDims = jpgImage.scale(1); // Modifier la taille de l'image


      var logo_startX = 215;
      var logo_startY = pageHeight - 100;

      page.drawImage(jpgImage, {
        x: logo_startX ,
        y: logo_startY,
        width: jpgDims.width,
        height: jpgDims.height,
    });

      ////// FIN HOTEL NAME ////////////////

      /////// TITRE CAISSE //////////////
      let caisse_title = `Caisse N° ${caisse["id"]}`
      console.log(caisse_title)
      var caisse_title_startX = 0;
      var caisse_title_startY = pageHeight - 110;
      var caisse_title_cellWidth = pageWidth
      var caisse_title_cellHeight = 40
 
      // Définir l'alignement du texte au centre
      const caisse_title_textWidth = font.widthOfTextAtSize(caisse_title,18);
      const caisse_title_textHeight = font.heightAtSize(18);
      var textX = caisse_title_startX + (caisse_title_cellWidth - caisse_title_textWidth) / 2;
      var textY = caisse_title_startY - (caisse_title_cellHeight - caisse_title_textHeight) - 3;

      page.drawRectangle({
                  x: 0 ,
                  y: textY - 10 ,
                  width: pageWidth,
                  height: caisse_title_textHeight + 20,
                  color: rgb(137 / 255, 207 / 255, 243 / 255),
                  borderColor: rgb(204 / 255, 234 / 255, 187 / 255),
                  borderWidth: 1,
          });
      
      const caisse_title_cell = page.drawText(
              caisse_title,
              {
                x: textX,
                y: textY, 
              size:18,
              
              }
            );
            
      /////// FIN TITRE CAISSE //////////////
      




      
      
      
      /////////// FIN HEAD ////////////////////////////////////////////////////////////////////////////////////////////////////////


      const caisse_pres_suiv = [caisse,caisse_prec,caisse_suiv]
      /////////// CORP ////////////////////////////////////////////////////////////////////////////////////////////////////////
      for (let c = 0; c < caisse_pres_suiv.length; c++){

          var monnaie = [['Type', 'Nombre', 'Somme']]
          var encaissement = ""
          var info = []
          var depenses = [['Montant', 'Libelé'],]
          var commentaire = caisse_pres_suiv[0]["commentaire"]

///////////////////////////////// TRAITEMENT DES DONNÉES //////////////////////////////////////////////////////////////////////
          
          if (caisse_pres_suiv[c] != null){

            let Caisse = caisse_pres_suiv[c]

            var ignored = ['emeteur','receveur','obj_name','editions','deleted','caisse_precedente','liste_depenses','caisse_cashIn','coffre_cashIn','commentaire']

            var encaissement = ""

            if (Caisse["caisse_cashIn"]){
              encaissement = "/ Caisse"
            }else if (Caisse["coffre_cashIn"]){
              encaissement = "/ Coffre"
            }

            var replaced = [
              ["Emeteur_name" , "Emeteur"],
              ["Receveur_name" , "Receveur"],
              ["Total_cashIn" , "Encaissé"],
              ["Total_depense" , "Depensé"],
              ["Total_caisse" , "Total caisse"],
              ["Total_ecart" , "Ecart"],
            ]

            Caisse['total_cashIn'] = Caisse['total_cashIn'].toString() + " " + encaissement
            for(let key in Caisse){

              var editedKey = key
              editedKey = editedKey.charAt(0).toUpperCase()+ editedKey.slice(1)

              console.log(replaced.length)
              for (let r = 0 ; r < replaced.length; r++){
                editedKey = editedKey.replace(replaced[r][0],replaced[r][1])
              }
              
              if (key == "monnaie"){
                for (let key2 in Caisse[key]){
                
                    let ligne = [key2,Caisse[key][key2][0].toString(),Caisse[key][key2][1].toString()]
                    monnaie.push(ligne)
                  
                }

              }else if (key == "liste_depenses" && Caisse[key] != null && Caisse[key] != undefined ) {
                console.log(Caisse[key])
                for (let d = 0 ; d < Caisse[key].length; d++){
