<template>
  <!-- HEAD -->
  <div class="page-head d-flex  w-100 mb-1 fw-bold" :class="{'main_dark':this.$store.state.dark == true,}">
    <h1 class="  w-100 mb-1 fw-bold" :class="{'main_dark':this.$store.state.dark == true,}">Connexion</h1>
    <DarkThemeBtn/>
  </div>

  <!-- CORP  -->
  <div  class="w-100  h-100 d-flex align-items-center">
    <div class="FormContainer m-auto container-fluide  text-center p-2 rounded"  :class="{'dark_1':this.$store.state.dark == true,}">
        
      
      <h4 class="mt-3">Conectez vous </h4>
        <form @submit.prevent="Login" class="text-start m-auto p-3 small">
          
          <!-- password recup form  -->
          <div v-if="this.passwordRecup" class="form-group passwordRecup w-100 border  rounded text-end pt-0" :class="{'border-dark':this.$store.state.dark == false,}">
            <img @click="passwordRecup = false" src="@/assets/icones/cancel3.png" alt="close" class="me-2 mt-1" style="width:20px; height:20px">
            <div class="container-fluide text-center p-3">
              <strong class="text-center">Vous avez oubliez votre mot de passe ? </strong>
              <p class="ms-2 small text-center">Entrez votre email pour recevoir un mail de recuperation</p>
              <input type="email" class="form-control" id="email" v-model="email" placeholder="Entre votre email" autocomplete="email" />
              <button type="submit" class=" valide-btn rounded mt-4 w-50" >Envoyer</button>
            </div>
          </div>

          <!-- sign in form  -->
          <div v-if="!this.passwordRecup" class="container fluide">
                <div v-if="conexionfailedMessage.length > 0" class="container-fluide text-center"><span class="errors">{{ conexionfailedMessage }}</span></div>

                <div class="form-group">
                  <label for="email" class="ms-2 small"  >Email:</label>
                  <input type="email" class="form-control" id="email" v-model="email" placeholder="Entre votre email" autocomplete="email"/>
                  <!-- <div v-if="EmailErrors">
                    <ul>
                      <li v-for="Error in emailErrors" class="extraSmall"> {{ Error }}</li>
                    </ul>
                  </div> -->
                </div>

                <div class="form-group">
                  <label for="password1" class="ms-2 small">Password:</label>
                  <input type="password" class="form-control" id="password1" v-model="password" placeholder="Entre votre mot de passe" />
                  <!-- <div v-if="PasswordErrors">
                    <ul>
                      <li v-for="Error in passwordErrors" class="extraSmall"> {{ Error }}</li>
                    </ul>
                  </div> -->
                </div>

                <div class="form-group mt-4 mb-2 text-center">
                  <button type="submit" class=" valide-btn rounded w-100" >Log in</button>
                </div>
          </div>

          <div class="mt-3">
            <div class="extraSmall  " >Mot de passe oublié ?  <span  @click="this.passwordRecup = true" class="text-primary " style="cursor:pointer"> click ici </span> </div>
            <div class="extraSmall  " >vous n'avez pas de compte ?  <span href="#/signup" class="text-primary " style="cursor:pointer"> Créer un compte </span> </div>
          </div>

    

        </form>


    </div> 
  </div>

</template>

<script>


import config from '@/config.js'
import DarkThemeBtn from '@/components/DarkThemeBtn.vue'

// import { axios_request,handel_errors} from '@/util'
// import displayCaisse from '@/components/displayCaisse.vue'



export default {
  // name: 'HomeView',
  components:{DarkThemeBtn},

  data(){return{
    email: '',
    password: '',
    logedIn:false,
    conexionfailedMessage:'',
    tentatives:0,
    passwordRecup:false,
   

    }
  },
  mounted(){
   
  },
  methods:{
    async Login() {
      this.conexionfailedMessage = ''
      try {
        const passwordHash = CryptoJS.SHA256(this.password).toString()
        const url = `${config.apiUrl}signin`

        let payload = { email: this.email , password: passwordHash}
        const response = await axios.post(url, payload)

        // AJOUTER COOKIE AU NAVIGATEUR
        Cookies.set('partner', response.data.jwt)

        this.$store.dispatch('checkUser')
        location.reload()
      
        // console.log(response.data)
      } catch (err) {
        console.log(err)
        // console.error(err.response.data.detail)
        if (err.response.data.detail == "Incorect email or password"){
          this.conexionfailedMessage = "Email ou Mot de passe invalide, Merci de rectifier"
          this.tentatives += 1
          if (this.tentatives == 4 ){
            this.passwordRecup =true
          }
        }
      }
    }
   
  }

}
</script>

