<template>
  
   <!-- colonne 1 -->
  <div class="  ms-2 calander-colonne  colonne p-1  h-100 rounded" :class="{'dark_2':this.$store.state.dark == true, }" >
    
    <div class="horloge w-100 p-0 m-0 position-relative">
  
     <div   class="bg-1 date  rounded pb-1 ">
      {{ date }}
    </div>

    </div>

    <div  class="bg-1 calander p-2 rounded m-0 mt-2">
      <FullCalendar ref="calendar" :options="calendarOptions"  />
    </div>

    <div class="bg-1 reporting mt-2 p-2 rounded d-flex flex-column justify-content-">
      
      <!-- <div class=" bg-1 horloge m-0 p-0 pt-1 rounded">
        <Horloge :time_display="true"/>
      </div> -->


      <div class="w-100 mt-2 text-start fw-bold small ">Consignes du jour : 04</div>
      <div class="w-100 text-start fw-bold small ">Taxis du jour : 04</div>
      <div class="w-100 text-start fw-bold small ">Reveilles du jour : 04</div>




      <button class="valide-btn rounded  m-1 mt-2 "> Imprimer </button>

    </div>
  
  </div>
    <!-- #################################### -->
  





</template>

<script>
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'

import Horloge from '@/components/dashboard/widgets/Horloge_widget.vue'
export default {
  // name: 'ConsDash',
  props: {
    msg: String
  },
  components:{FullCalendar,Horloge},
  data(){ return {
    calendarOptions: {
        plugins: [ dayGridPlugin, interactionPlugin ],
        dateClick: this.handleDateClick,
        initialView: 'dayGridMonth',
        fixedWeekCount: false,
        // dayCellContent: this.customDayCellContent,
      },
      date:"",
  
  }

  },
  mounted(){
    this.select_today()

  },
  methods:{
    handleDateClick(info){
    
    this.date = info.dateStr
   
    if (info.dayEl != undefined){
      // Changer la couleur de fond de la cellule du jour sélectionné
      info.dayEl.style.backgroundColor = 'black';
      if (this.previousSelectedDayEl) {
        this.previousSelectedDayEl.style.backgroundColor = '';
      }
      this.previousSelectedDayEl = info.dayEl;
    }

    

  },

  select_today(){
    let today = new Date()
    let jour = today.getDate()
    let mois = today.getMonth() + 1
    let annee = today.getFullYear()
   
    if(mois < 10){mois = `0${mois}`}
    if(jour < 10){jour = `0${jour}`}
    
    let date_du_jour = `${annee}-${mois}-${jour}`

    
   
    const calendarApi = this.$refs.calendar.getApi();
    calendarApi.today(); // Obtenir la date actuelle du calendrier
    const calendarElement = this.$refs.calendar.$el; // Élément racine du calendrier FullCalendar
    const allDateCells = calendarElement.querySelectorAll('.fc-day'); // Recherche de toutes les cellules de date du calendrier
    // Trouver l'élément correspondant à la date spécifique
    let htmlEl = undefined
    for (const cell of allDateCells) {
      const cellDate = cell.getAttribute('data-date'); // Obtenir la date associée à la cellule
      if (cellDate === calendarApi.getDate().toISOString().substring(0, 10)) {
        htmlEl = cell
      }}

      const info = {
        dateStr:date_du_jour,
        dayEl:htmlEl
      }
     
    this.handleDateClick(info)

  },
  },
  watch: {
    date(newDate, oldDate) {this.$emit('nouvelle-date', newDate);},
  },
}
</script>

